import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'
import { BrowserRouter as Router, Route, Link } from 'react-router-dom'
import { Layout, Menu, Dropdown, message, Breadcrumb, Icon, Footer, Button, Input, Row, Col, Tree, Pagination } from 'antd'
import CommonTable from '../../common/CommonTable/CommonTable'
import app from '../images/app.png'
import styles from './styles.css'
import ReactEcharts from 'echarts-for-react'


class CheckItem extends Component {
    constructor() {
        super()
    }

    itemCircleData = [
        { key: '', value: 270 },
        { key: '', value: 150 },
        { key: '', value: 72 },
        { key: '', value: 260 },
        { key: '', value: 287 },
    ]

    getPercent(value) {
        let total = 0;
        for (let i = 0; i < this.itemCircleData.length; i++) {
            total += this.itemCircleData[i].value
        }

        return Math.round(value * 10000 / total) / 100 + '%'
    }
    itemOption = {
        title: {
            show: false,
            text: '',
            subtext: '',
            x: 'center'
        },
        legend: {
            show: false,
            orient: 'vertical',
            left: 'left',
            data: ['', '', '', '', '']
        },
        series: [
            {
                name: '',
                type: 'pie',
                radius: '100%',
                center: ['50%', '50%'],
                label: {
                    normal: {
                        position: 'inner'
                    }
                },
                color: ['rgba(0,0,0,0)'],
                data: this.itemCircleData.map((data) => {
                    return { value: data.value, name: this.getPercent(data.value) }
                }),
                startAngle: 88
            },
            {
                name: '',
                type: 'pie',
                radius: '80%',
                center: ['50%', '50%'],
                data: this.itemCircleData.map((data) => {
                    return { value: data.value, name: `${data.key}\n${data.value}` }
                }),
                color: ['RGB(42,185,251)', 'RGB(236,198,97)', 'RGB(66,184,198)', 'RGB(255,133,130)', 'RGB(97,222,164)'],
                itemStyle: {
                    emphasis: {
                        shadowBlur: 10,
                        shadowOffsetX: 0,
                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                },
                startAngle: 88
            }
        ]
    }

    onChartReady() {

    }

    onEvents = {

    }
    render() {
        return (
            <div>
                <h4 style={{ color: 'RGB(3,169,245)', fontSize: '20px' }}>
                    <Icon type="bars" />
                    <span> </span>
                </h4>
                <div style={{ padding: 30, width: '40%', display: 'inline-block' }}>
                    <p>1.</p>
                    <p style={{paddingLeft:15}}>1)</p>
                    <p style={{paddingLeft:15}}>2)</p>
                    <br />
                    <p>2.</p>
                    <p style={{paddingLeft:15}}>1)</p>
                    <p style={{paddingLeft:15}}>2)</p>
                    <br />
                    <p>3.</p>
                    <p style={{paddingLeft:15}}>1)</p>
                    <p style={{paddingLeft:15}}>2)</p>
                    <p style={{paddingLeft:15}}>3)</p>
                    <p style={{paddingLeft:15}}>4)</p>
                    <br />
                    <p>4.</p>
                </div>
                <div style={{ width: '60%', display: 'inline-block' }}>
                    <ReactEcharts
                        option={this.itemOption}
                        notMerge={true}
                        lazyUpdate={true}
                        onChartReady={this.onChartReady}
                        onEvents={this.onEvents} />
                </div>
            </div>
        )
    }
}



export default CheckItem


