import React,{Component} from 'react'
import {BrowserRouter as Router,Route,Link} from 'react-router-dom'
import { Tree,Row,Col,Input,Button,Select } from 'antd'
import app from '../images/app.png'
import CommonTable from '../../common/CommonTable/CommonTable'
import InInterfaceConfig from './inInterfaceConfig.json'
import AddInInterfaceConfig from './AddInInterfaceConfig'

const TreeNode = Tree.TreeNode
const Option = Select.Option

export default class InInterfaceConfigTab extends Component{

  constructor(){
    super()
    this.state={
      showAddDialog:false,
    }
  }

  rowSelectionData = (selectData) => {
    this.setState({
      selectData: selectData
    })
  }

  //
  setAddDialogState=()=>{
    this.setState({
      showAddDialog: false,
    });
  }

  render(){

    const {commonTable,dispatch,menuState} = this.props
	  const url = 'http://www.xxx.com?xs'

	  const pageSize = 10

    const columns = [{
        title: '',
        dataIndex: 'jkmc',
        key: 'jkmc'
      }, {
        title: '',
        dataIndex: 'jkbq',
        key: 'jkbq',
      }, {
        title: '',
        dataIndex: 'jkfl',
        key: 'jkfl',
      },{
        title: '',
        dataIndex: 'jkms',
        key: 'jkms',
      },{
        title: '',
        dataIndex: 'jkzt',
        key: 'jkzt',
      },{
        title: '',
        key: 'action',
        render: (text, record, index) => (
          <div>
            <Button type="primary" ghost onClick={()=>{console.log(text, record, index)}}></Button>
            <Button style={{marginLeft:'10px'}} type="primary" ghost onClick={()=>{console.log(text, record, index)}}></Button>
          </div>
          
        ),
      }]

    const data = InInterfaceConfig.data

    const rowSelection = true
    const pagination = data?{total:data.length,current:1,pageSize:pageSize}:{}
    const tableData = data?{columns,data,pagination,pageSize,url,rowSelection}:{columns,pageSize,url}

    return (
      <div>
        <div style={{background:"#E5E5E5",height:'43px',lineHeight:'43px',paddingLeft:'20px'}}>
          <img src={app}/>
          <span style={{fontSize:'16px',background:"#E5E5E5",color:"#03a9f5",marginLeft:'10px'}}></span>
        </div>

        <Row type="flex">
          <Col span={3}>
            <div>
              <Tree
                showLine
                defaultExpandedKeys={['0-0-0']}
                onSelect={this.onSelect}
              >
                <TreeNode title="" key="0-0">
                  <TreeNode title="" key="0-0-0" />
                  <TreeNode title="" key="0-0-1" />
                  <TreeNode title="" key="0-0-2" />
                </TreeNode>
              </Tree>
            </div>
          </Col>
          <Col span={21}>
            <div style={{padding:'0px 40px'}}>
              <div style={{fontSize:"14px",height:'76px',lineHeight:'76px'}}>
                <div style={{float:'left'}}>: <Input size="default"  style={{width:'100px'}} /> </div>
                <div style={{float:'left'}}>: 
                  <Select placeholder="" style={{width:'80px'}} >
                    <Option value=""></Option>
                    <Option value=""></Option>
                  </Select>  
                </div>
                <div style={{float:'left',marginLeft:'40px'}}>
                  <Button type="primary"></Button>
                  <Button type="Default" style={{marginLeft:'10px'}}></Button>
                </div>
                <div style={{float:"right"}}>
                  <Button type="primary" style={{marginRight:'10px'}} onClick={() => {
                      
                    }}></Button>
                  <Button type="primary" style={{marginRight:'10px'}} onClick={() => {
                      this.setState({
                        showAddDialog: true,
                      });
                    }}></Button>
                  <Button type="warning" style={{marginRight:'10px'}} 
                    onClick={{}}
                    ></Button>
                  <Button type="danger" style={{marginRight:'10px'}} onClick={()=>{}} ></Button>
                  <Button type="primary" style={{}} onClick={() => {}}></Button>
                </div>
              </div>
              <CommonTable {...({tableData,commonTable,dispatch})}  rowSelection={this.rowSelectionData}/>
              <AddInInterfaceConfig showAddDialog={this.state.showAddDialog} setAddDialogState={this.setAddDialogState}/>
            </div>
          </Col>
        </Row>
      </div>
    )
  }
}


