import React,{Component} from 'react'
import { Modal,Button,Form,Input,AutoComplete,Select,Row,Col } from 'antd'

const FormItem = Form.Item
const Option = Select.Option

class AddDsManager extends React.Component {
 
  constructor(){
    super()
    this.state = {
    loading: false,
    visible: false
  }

  }
  
  handleOk = () => {
    this.setState({ loading: true });
    if(this.props.setAddDialogState) {
      this.props.setAddDialogState()
    }

    this.setState({ loading: false, visible: false });
  }
  handleCancel = () => {
    this.setState({ visible: false });
    if(this.props.setAddDialogState){
      this.props.setAddDialogState()
    }
  }

    componentWillReceiveProps(newProps) {

    let oldData = this.props.showAddDialog;
    let newData = newProps.showAddDialog;
    console.log(newProps)

    if (oldData!=null &&(oldData != newData)) {
      this.setState({ visible: newData })
    }

  }


  render() {

    const { visible, loading } = this.state
    const { getFieldDecorator } = this.props.form

    const formItemLayout = null
    
    return (
      <div>
        <Modal
          visible={visible}
          title=""
          width='800'
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          footer={[
            <Button key="test" type="primary" loading={loading} onClick={this.handleOk}>
              
            </Button>,
            <Button key="submit" type="warning"  loading={loading} onClick={this.handleOk}>
              
            </Button>,
            <Button key="back" onClick={this.handleCancel}></Button>,
          ]}
        >
          <Form 
            layout='vertical'
            onSubmit={this.handleSubmit}>
            <Row gutter={32}>
              <Col span={12}>
                <FormItem
                  {...formItemLayout}
                  label=""
                >
                  <Input />
                </FormItem>
              </Col>
              <Col span={12}>
                <FormItem
                  {...formItemLayout}
                  label=""
                  hasFeedback
                >
                  {getFieldDecorator('select', {
                    rules: [
                      { required: true, message: '' },
                    ],
                  })(
                    <Select placeholder="">
                      <Option value="ORACLE">ORACLE</Option>
                      <Option value="MYSQL">MYSQL</Option>
                    </Select>
                  )}
                </FormItem>
              </Col>
            </Row>
            <Row gutter={32}>
              <Col span={12}>
                <FormItem
                  {...formItemLayout}
                  label=""
                  hasFeedback
                >
                  {getFieldDecorator('select', {
                    rules: [
                      { required: true, message: '' },
                    ],
                  })(
                    <Select placeholder="">
                      <Option value="JDBC">JDBC</Option>
                      <Option value="OTHER">OTHER</Option>
                    </Select>
                  )}
                </FormItem>
              </Col>
              <Col span={12}>
                <FormItem
                  {...formItemLayout}
                  label="IP"
                >
                  <Input />
                </FormItem>
              </Col>
            </Row>
            <Row gutter={32}>
              <Col span={12}>
                <FormItem
                  {...formItemLayout}
                  label=""
                >
                  <Input />
                </FormItem>
              </Col>
              <Col span={12}>
                <FormItem
                  {...formItemLayout}
                  label=""
                >
                  <Input />
                </FormItem>
              </Col>
            </Row>
            <Row gutter={32}>
              <Col span={12}>
                <FormItem
                  {...formItemLayout}
                  label=""
                >
                  <Input />
                </FormItem>
              </Col>
              <Col span={12}>
                <FormItem
                  {...formItemLayout}
                  label=""
                >
                  <Input />
                </FormItem>
              </Col>
            </Row>
            <Row gutter={32}>
              <Col span={12}>
                <FormItem
                  {...formItemLayout}
                  label=""
                >
                  <Input />
                </FormItem>
              </Col>
              <Col span={12}>
                <FormItem
                  {...formItemLayout}
                  label="URL"
                >
                  <Input />
                </FormItem>
              </Col>
            </Row>
          </Form>
        </Modal>
      </div>
    )
  }
}

const AddDsManagerData = Form.create()(AddDsManager)

export default AddDsManagerData


